@import "../../assets/scss/variable.scss";


.popup-line {
    // box-shadow: 0vh 0vh 1vh var(--box-shadow-1);
    box-shadow: 0.5vh 0.5vh 1vh var(--text-color);
    background: var(--bkg-color-2);
    position: absolute;
    z-index: 5;
    p {
        margin: 0;
    }
    ul {
        text-align: left;
    }
}
.popup-title {
    border: 1px solid var(--text-color);
    text-align: left;
    padding: 4px 11px 5px;
    // font-size: $ft-s;
    font-size: 10px;
    font-weight: bold;
    cursor: pointer;
    .close-btn {
        position: absolute;
        right: 8px;
        top: 2px;
        font-size: 0.9rem;
    }
}
.highlight {
    z-index: 10;
    .popup-title {
        background-color: #00ffad;
        color: #222!important;
        .close-btn {
            color: #222!important;
        }
    }
}
.popup-title:hover {
    border-color: #00ffad;
}
.popup-content {
    border-left: $b-w solid var(--text-color);
    border-right: $b-w solid var(--text-color);
    border-bottom: $b-w solid var(--text-color);
    text-align: left;
}

//== DEVICE SCREEN ==//
/*대부분의 스마트폰 모바일 기기(세로)*/
@media (min-width: 320px) and (max-width: 480px) {
	.popup-line {
        position: relative;
        width: 100%!important;
        max-width: 100%!important;
        margin-bottom: 5px;
        top: auto!important;
        left: auto!important;
        box-shadow: 0.2vh 0.2vh 0.5vh var(--text-color)!important;
    }
}

/*저해상도 태블릿, 모바일(가로)*/
@media (min-width: 481px) and (max-width: 767px) {
	.popup-line {
        position: relative;
        width: 100%!important;
        max-width: 100%!important;
        margin-bottom: 5px;
        top: auto!important;
        left: auto!important;
        box-shadow: 0.2vh 0.2vh 0.5vh var(--text-color)!important;
    }
}