@import "../../assets/scss/variable";

#Nav {
	z-index: 15;
	position: fixed;
	padding: 10px 12px 10px 12px;
	display: flex;
	align-items: center;
	line-height: 1;
	width: 100%;
	overflow: auto;
	-ms-overflow-style: none; 
	scrollbar-width: none;
	// border-bottom: 1px solid var(--border-color);
	// background-color: var(--bkg-color);
	// box-shadow: 0vh 0vh 1vh var(--box-shadow-2);
	// opacity: 0.95;
	.nav-toggle {	
		margin: 0px 15px 0px 3px;
		transition: all ease 0.1s 0s;
		-moz-transition: all ease 0.1s 0s;
		-webkit-transition: all ease 0.1s 0s;
		text-decoration: none;
		// color: var(--text-color);
		color: #eee;
		a {
			margin-top: 0.7rem;
			display: block;
		}
	}
	.active {
		// border-bottom: $b-w solid var(--text-color) !important;
		border-bottom: $b-w solid #eee !important;
		font-weight: bold;
		line-height: 1.2;
		transition: all ease 0.1s 0s;
		-moz-transition: all ease 0.1s 0s;
		-webkit-transition: all ease 0.1s 0s;
		// font-family: 'Common-Bold';
		letter-spacing: $l-s-bold;
	}
}
#Nav::-webkit-scrollbar {
	display:none;
} 

//== DEVICE SCREEN ==//
/*대부분의 스마트폰 모바일 기기(세로)*/
@media (min-width: 320px) and (max-width: 480px) {
	#Nav {
		padding: 10px 8px;
	}
}

